import { Component } from '@angular/core';
import { ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ActionSheetController } from 'ionic-angular'

import { ChangeDetectionStrategy } from "@angular/core";

import { RestProvider } from '../../providers/rest/rest';

import { LoadingController } from 'ionic-angular';

import { Storage } from '@ionic/storage';

import { AlertController } from 'ionic-angular';

import { DomSanitizer } from '@angular/platform-browser';

/**
 * Generated class for the CalendarioPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-calendario',
  templateUrl: 'calendario.html',
})
export class CalendarioPage {


dataUser:any={};
 eventSource;
    viewTitle;

    isToday:boolean;

    calendar = {
      locale: 'es-MX',
      startingDayWeek: '1',
        mode: 'day',
        formatMonthTitle:'LLLL',
        formatDayTitle:'LLL d',
        currentDate: new Date(),
        dateFormatter: {
            formatMonthViewDay: function(date:Date) {
                return date.getDate().toString();
            },
            formatMonthViewDayHeader: function(date:Date) {
                return 'LLL';
            },
            formatMonthViewTitle: function(date:Date) {
                return 'testMT';
            },
            formatWeekViewDayHeader: function(date:Date) {
                return 'MonWH';
            },
            formatWeekViewTitle: function(date:Date) {
                return 'testWT';
            },
            formatWeekViewHourColumn: function(date:Date) {
                return 'testWH';
            },
            formatDayViewHourColumn: function(date:Date) {
                return 'testDH';
            },
            formatDayViewTitle: function(date:Date) {
                return 'testDT';
            }
        }
    };



  constructor(public navCtrl: NavController,private cdr: ChangeDetectorRef,public restProvider: RestProvider, public loadingCtrl: LoadingController, public storage: Storage,public actionSheetCtrl: ActionSheetController,private DomSanitizer: DomSanitizer,public alertCtrl: AlertController) {

  }
ionViewDidEnter(){


   this.storage.get('usr_tok_byae').then((result) => {
    
    if(result){
      this.dataUser = result;
      this.loadEvents();
      console.log(this.dataUser);
 
    } else {
    console.log('error-nologin');
    }
    });

   this.storage.get('usr_alrt_byae').then((result) => {
    
    if(result){
      this.presentAlert('Nota Importante');
      this.storage.set(`usr_alrt_byae`, false);
    } 

    });




}
 

calcularPorConfirmar(events) { 
  let sum = 0;
  events.forEach((event) => {
    if(event.estado == 0)
      sum += 1;
  })
  return sum
}

previousMonth() {
console.log(this.calendar.currentDate);
if(this.calendar.mode=="month"){
   var ff = new Date(this.calendar.currentDate.setMonth(this.calendar.currentDate.getMonth()-1));
   this.calendar.currentDate =  ff;
}
if(this.calendar.mode=="day"){
     var gg = new Date(this.calendar.currentDate.setDate(this.calendar.currentDate.getDate()-1));
   this.calendar.currentDate =  gg;

}



}



  reintentarAlert(funcionEnviar){

    var mensaje = `<div>  
                      <p>No hemos podido conectar. 
                      Verifica tu conexión a Internet para continuar</p>
                      
                   <div>`;
    let alert = this.alertCtrl.create({
      title: 'Error de conexión',
      subTitle: this.DomSanitizer.bypassSecurityTrustHtml(mensaje), 
       buttons: [

           {
        text: 'Reintentar',
        handler: () => {
          funcionEnviar();
        }
      },
    ],
      enableBackdropDismiss: false
    });
    alert.present();

  }


nextMonth() {

if(this.calendar.mode=="month"){
    var ff = new Date(this.calendar.currentDate.setMonth(this.calendar.currentDate.getMonth()+1));
     this.calendar.currentDate =  ff;
}
    
if(this.calendar.mode=="day"){
     var gg = new Date(this.calendar.currentDate.setDate(this.calendar.currentDate.getDate()+1));
   this.calendar.currentDate =  gg;

}


}


    presentAlert(titulo) {
    let alert = this.alertCtrl.create({
      title: titulo,
      subTitle: 'Por favor crea las reservas que tengas por otros medios en tu agenda para mostrar tu disponibilidad real y  evitar conflictos de tiempo con las reservas en línea.',
      buttons: [
      {
        text: 'Cerrar',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      },
      {
        text: 'Crear Reserva',
        handler: () => {
          this.navCtrl.push('NreservaPage');
        }
      }
    ]
    });
    alert.present();
  }


 loadEvents() {
       // this.eventSource = this.createRandomEvents();

       let loading = this.loadingCtrl.create({content : "Obteniendo Reservas..."});
  loading.present();

        this.restProvider.getEventosUserNC({idEmpleado:this.dataUser.idEmpleado, admin:this.dataUser.tipo, idCentro:this.dataUser.idCentro})
    .then(data => {
   		   loading.dismiss();
      if(data){

      this.eventSource = data.map(item=>{

      var arrayTemp = item;

      if(item.idReservaManual){

      arrayTemp.title=item.detalle;

      var date1 = new Date(item.y, item.m-1, item.d, item.h, item.min);
      var date2 = new Date(item.y, item.m-1, item.d, item.h2, (item.min2-2));
      var date22 = new Date(item.y, item.m-1, item.d, item.h2, (item.min2));

        arrayTemp.startTime=date1;
        arrayTemp.endTime=date2;
        arrayTemp.endString = date22;
        arrayTemp.allDay=false;   
        arrayTemp.servicio=' ';
        arrayTemp.estado=77; 
          return arrayTemp; 
      }
      else{
         
         arrayTemp.title=item.clienteReferencia ? item.clienteReferencia : item.nombreCliente;

         var date1 = new Date(item.y, item.m-1, item.d, item.h, item.min);

      var date2 = new Date(item.y, item.m-1, item.d, item.h2, (item.min2-2));
      var date22 = new Date(item.y, item.m-1, item.d, item.h2, (item.min2));




         arrayTemp.startTime=date1;
         arrayTemp.endTime=date2;
         arrayTemp.endString = date22;

         arrayTemp.allDay=false;
         arrayTemp.servicio=item.nombreServicio;

         //console.log(arrayTemp);

         return arrayTemp;
        }




       });
       }
       else{
       console.log('err conex');
      this.reintentarAlert(this.loadEvents.bind(this));
       }
     
    });


    }


addReserva(){
    const actionSheet = this.actionSheetCtrl.create({
      title: 'Tipo de reserva',
      buttons: [
        {
          text: 'Nueva Reserva',
        
          handler: () => {
           //console.log('Destructive clicked');
           this.navCtrl.push('NreservaPage');

          }
        },{
          text: 'Nueva reserva de tiempo',
          handler: () => {
           this.navCtrl.push('NreservatPage');
          }
        },{
          text: 'Salir',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked'); 
          }
        }
      ]
    });
    actionSheet.present();
  }



goDetalle(event){
	
	console.log(event);

  if(event.idReservaManual){console.log('rmanual');}
  else{  this.navCtrl.push('DetalleReservaPage', {idCita:event.idCita});}
	   
}
 showItems(item) {
      console.log(item);
      //return item.filter(e=>e.estado == 0).length;
    }


    onViewTitleChanged(title) {
        this.viewTitle = title;
    }

    onEventSelected(event) {
        console.log('Event selected:' + event.startTime + '-' + event.endTime + ',' + event.title);
    }

    changeMode(mode) {
        this.calendar.mode = mode;
    }

    today() {
        this.calendar.currentDate = new Date();
    }

    onTimeSelected(ev) {
        console.log('Selected time: ' + ev.selectedTime + ', hasEvents: ' +
            (ev.events !== undefined && ev.events.length !== 0) + ', disabled: ' + ev.disabled);
            // this.calendar.currentDate =  ev.selectedTime;
    }

    onCurrentDateChanged(event:Date) {
    console.log(event);
    this.calendar.currentDate =  event;
        var today = new Date();
        today.setHours(0, 0, 0, 0);
        event.setHours(0, 0, 0, 0);
        this.isToday = today.getTime() === event.getTime();
    }

    createRandomEvents() {
        var events = [];
        for (var i = 0; i < 50; i += 1) {
            var date = new Date();
            var estado = Math.floor(Math.random() * 4) + 0;
            var eventType = Math.floor(Math.random() * 2);
            var startDay = Math.floor(Math.random() * 90) - 45;
            var endDay = Math.floor(Math.random() * 2) + startDay;
            var startTime;
            var endTime;
            if (false) {
                startTime = new Date(Date.UTC(date.getUTCFullYear(), date.getUTCMonth(), date.getUTCDate() + startDay));
                if (endDay === startDay) {
                    endDay += 1;
                }
                endTime = new Date(Date.UTC(date.getUTCFullYear(), date.getUTCMonth(), date.getUTCDate() + endDay));
                events.push({
                    title: 'All Day - ' + i,
                    startTime: startTime,
                    endTime: endTime,
                    allDay: true,
                    servicio: 'Corte de Pelo',
                     estado:estado,
                     pendientes:Math.floor(Math.random() * 4) + 0
                });
            } else {
                var startMinute = Math.floor(Math.random() * 24 * 60);
                var endMinute = Math.floor(Math.random() * 180) + startMinute;
                startTime = new Date(date.getFullYear(), date.getMonth(), date.getDate() + startDay, 0, date.getMinutes() + startMinute);
                endTime = new Date(date.getFullYear(), date.getMonth(), date.getDate() + endDay, 0, date.getMinutes() + endMinute);
                events.push({
                    title: 'Event - ' + i,
                    startTime: startTime,
                    endTime: endTime,
                    allDay: false,
                    estado:estado,
                    servicio: 'Corte de Pelo',
                     pendientes:Math.floor(Math.random() * 2) + 0
                });
            }
        }
        console.log(events);
        return events;
    }

    onRangeChanged(ev) {
        console.log('range changed: startTime: ' + ev.startTime + ', endTime: ' + ev.endTime);
    }

    markDisabled = (date:Date) => {
        var current = new Date();
        current.setHours(0, 0, 0);
        return date < current;
    };

    


}



